import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import './App.css';
import About from "./pages/about"
import Portfolio from './pages/portfolio'
import Contact from './pages/contact'

function App() {
  return (
    <Router>
      <div>
        <Switch>
          <Route exact path={"/Responsive-Portfolio/portfolio"}>
            <Portfolio />
          </Route>
          <Route exact path={"/Responsive-Portfolio/contact"}>
            <Contact />
          </Route>
          <Route exact path={"/Responsive-Portfolio/"}>
            <About />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
