import React from 'react';
import {Row} from '../grid'
import { Link } from "react-router-dom"

function buttonGroup() {
    return (
        <Row>
            <div className='col-sm-8 offset-sm-2 button-col'>
                <Link to = "/Responsive-Portfolio/">
                <button type="button" id="about-me-btn">About Me</button>
                </Link>
                <Link to = "/Responsive-Portfolio/portfolio">
                <button type="button" id="prt-btn">Portfolio</button>
                </Link>
                <Link to = "/Responsive-Portfolio/contact">
                <button type="button" id="cont-btn">Contact</button>
                </Link>
            </div>
        </Row>
    )
}
export default buttonGroup;